import uniRate from '@/components/uni-rate/uni-rate.vue'
//引入导航
import storenav from "@/components/store/storenav.vue"
//引入的热销组件
import storerecommended from "@/components/store/storerecommended.vue"
//引入宝贝的组件
import storebady from "@/components/store/storebady.vue"
//引入新品列表
import storenews from "@/components/store/storenews.vue"
//引入视频
// import storevideo from "@/components/store/storevideo.vue"
const app = getApp()
export default {
	//这是店铺
	data() {
		return {
			statusBar:0,
			items:"推荐",
			Month:0,
			day:0,
			storeid:"",
			Shopname:"",
			Whetherproprietary:false,//是否自营
			score:0,//分数
			storecredit:"",
			newslist:[],
			horizontallylist:[],
			bodylist:[]
		}
	},
	methods: {
		getChildnav(e){
			// console.log(e)
			this.items = e
			// console.log(this.items)
		}
	},
	onLoad(opction){
		const _this = this
		_this.storeid = opction.storeid
		_this.statusBar = app.globalData.statusBar
		let date = new Date()
		let Month = date.getMonth()+1; 
		let day = date.getDate()
		_this.Month = Month
		_this.day = day
		// console.log(_this.Month,_this.day)
		uni.request({
			url:"http://hbk.huiboke.com/api/store/getStoreInfo",
			data:{
				sid:_this.storeid
			},
			success(res) {
				if(res.data.code==0){
					let {store_name,is_platform_store,store_servicecredit,store_credit} = res.data.data
					_this.Shopname = store_name
					_this.score = store_servicecredit
					_this.storecredit = store_credit
					//判断是否自营
					if(is_platform_store){
						_this.Whetherproprietary = true
					}else{
						_this.Whetherproprietary = false
					}
				}
				
			}
		})
		//获取推荐
		uni.request({
			url:"http://hbk.huiboke.com/api/store/getStoreRecommendGoodList",
			data:{
				sid:_this.storeid,
				page:1,
				pageSize:10
			},
			success(res) {
				_this.horizontallylist = res.data.data.list
			}
		})
		//获取宝贝信息
		uni.request({
			url:"http://hbk.huiboke.com/api/store/getStoreGoodList",
			data:{
				sid:_this.storeid,
				page:1,
				limit:10
			},
			success(res) {
				_this.bodylist = res.data.data.list
			}
		})
		//获取商品的新品信息系
		uni.request({
			url:"http://hbk.huiboke.com/api/store/getNewStoreGoodList",
			data:{
				sid:_this.storeid,
				limit:10
			},
			success(res) {
				_this.newslist = res.data.data
			}
		})
	},
	components:{
		uniRate,
		storenav,
		storerecommended,
		storebady,
		storenews,
		// storevideo
	}
}
